
	@import 'styles/utils/vars.scss';
	.tos{
		&__checkbox-wrapper {
			display: flex;
			align-items: center;
			margin-bottom: 25px;
		}
		&__checkbox-tick {
			cursor: pointer;
			width: 20px;
			height: 20px;
			border: 1px solid #FFFFFF;
			border-radius: 4px;
			margin-right: 15px;
			display: flex;
			justify-content: center;
			align-items: center;
			img{
				width: 80%;
				height: auto;
			}
		}
		&__checkbox-text {
			font-family: 'Chivo';
			font-style: normal;
			font-weight: 400;
			font-size: 12px;
		}
	}
	.shopping-cart-window{
		top: 25px;
		position: sticky;
		// height: calc(100vh - 50px);
		@media screen and (max-width: 1200px) {
			height: auto;
		}
		overflow-y: auto;
		padding: 15px;
		scrollbar-width: thin;
		&::-webkit-scrollbar {
			width: 15px;
			@media screen and (max-width: $phoneWidth){
				width: 10px;
			}
		}

		&::-webkit-scrollbar-track {
			background-color: $background-color;
			border-radius: 5px;
		}

		&::-webkit-scrollbar-thumb {
			background-color: #555;
			border: 3px solid transparent;
			border-radius: 5px;
			background-clip: content-box;
		}
	}
	.checkout {
		display: flex;
		@media screen and (max-width: 1200px) {
			flex-direction: column;
		}
		&__window{
			width: 50%;
			&--mt{
				margin-top: 28px;
				@media screen and (max-width: 1200px) {
					margin-top: 0;
				}
			}
			@media screen and (max-width: 1200px) {
				width: 100%;
				
			}
		}
		&__fields{
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
			align-items: center;
			margin-top: 25px;
		}
		&__field-title	{
			font-family: 'Roboto';
			font-style: normal;
			font-weight: 700;
			font-size: 14px;
			line-height: 16px;
			margin-bottom: 15px;
		}
		&__field{
			flex: 0 0 calc(50% - 15px);
			@media screen and (max-width: $phoneWidth) {
				flex: 0 0 100%;
			}
		}
	}
	.payment-selection {
		&__title {
			font-family: 'Chivo';
			font-style: normal;
			font-weight: 400;
			font-size: 16px;
			margin-bottom: 25px;
		}
		&__methods {
			display: flex;
			flex-wrap: wrap;
		}
		&__method {
			flex: 0 0 25%;
			@media screen and (max-width: $smTabletWidth) {
				flex: 0 0 33%;
			}
			padding: 15px;
			cursor: pointer;
			filter: drop-shadow(0 0 0 transparent);
			transition: all 0.5s;
			&.active{
				filter: drop-shadow(0 0 10px var(--shadowColor))
			}
			img{
				width: 100%;
				height: auto;
			}
		}
	}


